
import XInput from 'vux/src/components/x-input/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Iconfont from '../../components/Iconfont'
export default {
name: 'Register',
components: {
XInput,
CellBox,
XButton,
Iconfont
},
data () {
return {
form: {
username: '',
verificat: '',
password: '',
repassword: ''
}
}
},
methods: {
get_verificat () {
},
register () {
if (!this.form.username) {
this.$vux.toast.text('请输入手机号/邮箱')
return
} else if (!this.form.verificat) {
this.$vux.toast.text('请输入验证码')
return
} else if (!this.form.password) {
this.$vux.toast.text('请输入密码')
return
} else if (!this.form.repassword) {
this.$vux.toast.text('请确认密码')
return
}
this.$http.post(`/api/register`, {
username: this.form.username,
verificat: this.form.verificat,
password: this.form.password,
repassword: this.form.repassword
}).then(res => {
console.log(res)
if (res.data.msg === 'success') {

} else {
this.$vux.toast.text(res.data.data)
}
})
}
}
}



